.remix-logo {
  max-width: 160px;
  margin-bottom: 24px;
  filter: drop-shadow(0 2px 12px #0288d155);
}
.about,
.offer {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 12px #0288d120;
  padding: 30px;
  margin: 36px auto;
  max-width: 740px;
}
.about h2, .offer h2 {
  margin-top: 0;
}
.contact {
  background: #0288d1;
  color: #fff;
  border-radius: 12px;
  padding: 32px;
  box-shadow: 0 4px 18px #0288d133;
  text-align: center;
  margin: 0 auto 30px auto;
  max-width: 740px;
}
.btn {
  background: #fff;
  color: #0288d1;
  text-decoration: none;
  padding: 12px 34px;
  font-weight: 600;
  font-size: 1.15em;
  border-radius: 22px;
  margin: 0 10px 12px 10px;
  display: inline-block;
  transition: background 0.2s, color 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px #0288d11c;
}
.btn:hover {
  background: #26c6da;
  color: #fff;
  box-shadow: 0 6px 14px #0288d155;
}
footer {
  text-align: center;
  color: #8ca1af;
  font-size: 1.06em;
  padding-bottom: 18px;
}
